.image-thumbnail {
  box-sizing: border-box;
  position: relative;
  width: 250px;
  height: 250px;
  display: flex;
  justify-content: center;
  align-items: center; }
  .image-thumbnail:hover p {
    opacity: 0; }
  .image-thumbnail:hover .image-wrapper {
    width: 100%;
    height: 100%;
    border: none;
    border-radius: 1rem;
    border: 3px solid #f2cb05; }
  .image-thumbnail .image-wrapper {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 160px;
    height: 160px;
    border: 2px solid #090b0d;
    border-radius: 50%;
    transition: width 400ms, height 400ms, border-radius 400ms;
    overflow: hidden; }
  .image-thumbnail video,
  .image-thumbnail img {
    width: 250px;
    height: 250px;
    object-fit: contain;
    background: none; }
  .image-thumbnail p {
    box-sizing: border-box;
    font-weight: 600;
    font-size: 1.2rem;
    margin: 0;
    color: #090b0d;
    text-align: center;
    position: absolute;
    bottom: 1rem;
    left: 0;
    padding: 0.5rem 0.5rem 0 0.5rem;
    padding-top: 0.5rem;
    font-size: 0.7rem;
    width: 100%;
    opacity: 1;
    transition: opacity 400ms; }
  @media screen and (max-width: 720px) {
    .image-thumbnail {
      margin: 1rem 0; }
      .image-thumbnail .image-wrapper {
        width: 100%;
        height: 100%;
        border-radius: 50%; }
      .image-thumbnail p {
        bottom: 0rem;
        background: #ffffffcc;
        padding: 0.5rem; } }
