.loading-icon {
  box-sizing: border-box;
  position: absolute; }
  .loading-icon .lds-ring {
    display: inline-block;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80px;
    height: 80px;
    opacity: 1;
    z-index: 3; }
  .loading-icon .lds-ring div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 64px;
    height: 64px;
    margin: 8px;
    border: 8px solid #090b0d;
    border-radius: 50%;
    animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: #f2cb05 transparent transparent transparent; }
  .loading-icon .lds-ring div:nth-child(1) {
    animation-delay: -0.45s; }
  .loading-icon .lds-ring div:nth-child(2) {
    animation-delay: -0.3s; }
  .loading-icon .lds-ring div:nth-child(3) {
    animation-delay: -0.15s; }

@keyframes lds-ring {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }
