.navigation button {
  cursor: pointer;
  position: fixed;
  top: 50%;
  border: none;
  width: 30px;
  height: 30px;
  transform: rotate(135deg);
  transition: box-shadow 0.2s; }
  .navigation button:hover, .navigation button:focus {
    outline: none;
    border-color: #f2cb05 !important; }

.navigation #nav-back-button {
  left: calc(5% - 30px);
  border-bottom: 2px solid #fefefe;
  border-right: 2px solid #fefefe; }
  .navigation #nav-back-button:hover, .navigation #nav-back-button:focus {
    box-shadow: 4px 4px 4px -2px #fefefe; }
  .navigation #nav-back-button:disabled {
    cursor: default;
    opacity: 0.2; }
    .navigation #nav-back-button:disabled:hover {
      box-shadow: none; }

.navigation #nav-next-button {
  right: calc(5% - 30px);
  border-top: 2px solid #fefefe;
  border-left: 2px solid #fefefe; }
  .navigation #nav-next-button:hover, .navigation #nav-next-button:focus {
    box-shadow: -4px -4px 4px -2px #fefefe; }

@media screen and (max-width: 720px) {
  .navigation button {
    width: 20px;
    height: 20px;
    top: auto;
    bottom: 5%; }
    .navigation button:hover, .navigation button:focus {
      border-color: #f2cb05 !important; }
  .navigation #nav-back-button {
    left: calc(5%);
    border-bottom: 2px solid #090b0d;
    border-right: 2px solid #090b0d; }
    .navigation #nav-back-button:hover, .navigation #nav-back-button:focus {
      box-shadow: 4px 4px 4px -2px #f2cb05; }
  .navigation #nav-next-button {
    right: calc(5%);
    border-top: 2px solid #090b0d;
    border-left: 2px solid #090b0d; }
    .navigation #nav-next-button:hover, .navigation #nav-next-button:focus {
      box-shadow: -4px -4px 4px -2px #f2cb05; } }
